@import url(<link href="https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Shadows+Into+Light&display=swap" rel="stylesheet">);
@import url(<link href="https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap" rel="stylesheet">);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Calligraffitti&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Megrim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.icons{width:5%;height:100%;position:fixed;padding:0.4rem;display:flex;flex-direction:column;align-items:center;list-style:none}.icons .vertical{border-left:2px solid white;height:390px;align-items:flex-start}.icons a img{padding:0.3rem;height:2rem;width:2rem}@media (max-width: 767px){.icons{display:none}}

html{
  scroll-behavior: smooth;
}
.details{padding-top:4rem;display:flex}.details .name{width:50%;text-align:center;color:blanchedalmond;padding:4rem 3rem;font-size:1.8rem}.details .photo{width:50%;height:100%;padding:2em 2em}.details .photo img{height:70%;width:70%;border-radius:50px 0px;border-bottom:2px solid white;border-top:2px solid white;padding:10px;transition:transform 0.2s}.details .photo img:hover{transform:scale(1.2)}.details .gmail{display:flex;flex-direction:column;width:5%;left:95%;position:fixed;color:white;align-items:center;justify-content:center}.details .gmail .vertical{border-left:2px solid white;height:290px;align-items:flex-start}.details .gmail p{font-size:1rem;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-text-orientation:mixed;text-orientation:mixed}@media (max-width: 767px){.details .photo{display:none}.details .name{width:100%;font-size:1.2rem;text-align:left}.details .icons{display:none}.details .gmail{display:none}}

*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Megrim", cursive;background-color:black}.container{width:80%;margin:2rem auto}.main-header{display:flex;justify-content:space-between;align-items:center;height:65px;width:100vw;padding:2rem 10vw;color:blanchedalmond;z-index:1}.main-header #menuicon{display:none}.main-header ul li a{text-decoration:none;color:blanchedalmond;text-transform:uppercase}.main-header .nav-links{display:flex;list-style:none}.main-header .nav-link{transition:transform 0.2s}.main-header .nav-link a{margin:0.1rem;padding:1rem 2rem}.main-header .nav-link:hover{transform:scale(1.3);background-color:blanchedalmond;box-shadow:0 0 5px blanchedalmond, 0 0 25px blanchedalmond, 0 0 50px blanchedalmond, 0 0 200px blanchedalmond}.main-header .nav-link:hover a{color:black}.main-header .logo{position:relative;z-index:1}.main-header .menu-icon{padding:26px 10px;position:relative;cursor:pointer;z-index:1;display:none}.main-header .menubutton{background-color:black;border-color:black;display:none}@media (max-width: 767px){.main-header #menuicon{display:block;z-index:1}.main-header .menubutton{background-color:black;border-color:black;display:flex;z-index:1}.main-header .menu-icon{display:block}.main-header .nav-links{display:none}.main-header .nav{position:absolute;top:0;left:0;opacity:1;flex-direction:column;justify-content:space-evenly;align-items:center;padding:10rem 0;width:100%;height:100%;color:blanchedalmond;background:black;-webkit-clip-path:circle(100% at center);clip-path:circle(100% at center)}.main-header .nav button{border-color:black;background:black;font-size:2rem;font-family:"Megrim", cursive}}

.about{width:90%;color:blanchedalmond;text-align:center;padding:10rem 7rem 20em}.about .para{font-family:"Julius Sans One", sans-serif;font-size:1.3rem;padding:4rem;text-align:justify}.about .para div{padding:10px 0}@media (max-width: 767px){.about{padding:15rem 0.1rem}.about .para{padding:20px;font-size:14px;text-align:left}}

.skill{padding:10rem 0 18rem}.skill h1{color:blanchedalmond;text-align:center;padding:3rem 2rem;font-family:"Megrim", cursive}.skill .skills{width:90%;display:flex;padding:10rem 0rem;margin:auto;align-items:center;justify-content:space-evenly}.skill .skills img{align-items:center;height:82px;width:82px}@media (max-width: 767px){.skill .skills img{height:50px;width:45px}}

.education{padding:0rem 0rem 10rem}.education h1{color:blanchedalmond;text-align:center;padding:2rem}.education .details{display:flex;flex-direction:row;padding:0.8rem 3rem}.education .details .images{display:flex;flex:1.5 1;flex-direction:column;justify-content:space-around;align-items:center}.education .details .text{flex:4 1;justify-content:space-between;display:flex;flex-direction:column}.education .details .text div{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start}.education .details .text div b,.education .details .text div p,.education .details .text div small{color:blanchedalmond;padding:0.2rem 1rem;font-family:"Julius Sans One", sans-serif;flex-direction:column;display:flex}.education .details .chart{flex:2 1;align-items:flex-start;justify-content:flex-start}@media (max-width: 767px){.chart{display:none}}

.work{padding:1rem 0.2rem 3rem;color:blanchedalmond;padding:3rem 5rem}.work h1{font-size:2rem;text-align:center;padding:2rem 0}.work .hvrbox,.work *{box-sizing:border-box}.work .hvrbox{position:relative;display:inline-block;overflow:hidden;max-width:100%;height:auto;display:flex;padding:0rem 0rem 3rem}.work .hvrbox .details{position:relative;top:90%;right:6%;display:flex;flex-direction:column;justify-content:center;font-family:"Julius Sans One", sans-serif}.work .hvrbox .details b{background-color:blanchedalmond;text-align:left;padding:2rem 2rem;justify-content:center;color:black}.work .hvrbox .details p{text-align:end}.work .hvrbox img{max-width:60%}.work .hvrbox .hvrbox-layer_bottom{display:block;height:200;width:217}.work .hvrbox .hvrbox-layer_top{opacity:0;position:absolute;top:0;left:0;right:0;bottom:0;width:60%;height:100%;background:rgba(0,0,0,0.6);color:#fff;padding:15px;transition:all 0.4s ease-in-out 0s}.work .hvrbox:hover .hvrbox-layer_top,.work .hvrbox.active .hvrbox-layer_top{opacity:1}.work .hvrbox .hvrbox-text{text-align:center;font-size:18px;display:inline-block;position:absolute;top:45%;left:45%;transform:translate(-50%, -50%);display:flex}.work .hvrbox .hvrbox-text a{padding:0 1rem}.work .hvrbox .hvrbox-text a button{border:none;color:black;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;border-radius:30px;font-size:16px}@media (max-width: 767px){.work{padding:0rem 0.5rem 3rem;height:100%;width:100%;display:flex;flex-direction:column;flex:1 1;align-items:center;justify-content:space-evenly}.work .hvrbox img{max-width:100%}.work .hvrbox{padding:0rem 0rem 2rem;display:flex;flex-direction:column}.work .hvrbox .hvrbox-layer_top{display:none}.work .hvrbox .details{right:0}.work .hvrbox .details b{font-size:1rem}.work .hvrbox .details p{font-size:0.75rem}}

